import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import {VideoDialogComponent} from '../video-dialog/video-dialog.component'
// import * as data from './players.json';
import { Player } from '../models/player.model';
import { HttpClient} from "@angular/common/http";

@Component({
  selector: 'app-players',
  templateUrl: './players.component.html',
  styleUrls: ['./players.component.css']
})
export class PlayersComponent implements OnInit {
  turnCard: Boolean;
  player_data: any;
  active_player: string;
  constructor(public dialog: MatDialog, private httpClient: HttpClient) {}

  ngOnInit(): void {

    this.httpClient.get("assets/players.json").subscribe(data =>{
      console.log(data);
      this.player_data = data;
    })
  }

  turn() {
    if (this.turnCard){
      this.turnCard = false;
    }else {
      this.turnCard = true;
    }
    console.log(this.turnCard)
  }


  getActive(player: Player) {
    // this.openDialog(player);
    if(this.active_player === player.naam){
      this.active_player="";
    }else{
      this.active_player = player.naam;
    }
    console.log(player.naam)
    
  }

  active(player: Player){
    if (this.active_player === player.naam) {
      return true
    }
    return false;
  }

  openDialog(player: Player) {
    this.dialog.open(PlayerDialog, {
      data: player
    });
  }

}

@Component({
  selector: 'dialog-data-example-dialog',
  templateUrl: 'player-dialog.html',
})
export class PlayerDialog {
  constructor(@Inject(MAT_DIALOG_DATA) public data: Player) {}
}